.home {
  flex-grow: 1;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home .title {
  width: 100%;
  font-size: 18px;
  text-align: center;
}

.home .title p {
  line-height: 50px;
}

.home .title button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 24px;
  margin-top: 30px;
  color: white;
  padding: 10px;
  border-radius: 999px;
  background-color: #3f71f4;
  font-family: "Righteous", cursive;
}

.title a {
  text-decoration: none;
  color: #12294B;
  
}

.title img {
  width: 40px;
  margin-left: 10px;
  height: 100%;
}

#gold {
  color: #86763F;
}

.home .person {
  width: 100%;
  margin-top: 20px;
}

.home .person img {
  width: 75%;
  margin-left: 12%;
}

@media (min-width: 800px) {
  .home {
    flex-direction: row;
    justify-content: space-between;
  }

  .home .title,
  .home .person {
    width: 50%;
    margin-top: 0;
    text-align: left;
    font-size: 25px;
  }

  .home .person img {
    width: 90%;
  }
}
